import React, { Component } from 'react';
// import logo from './logo.svg';
import LineComponent from './Components/LineComponent';
import CastingComponent from './Components/CastingComponent';
import './App.css';
import hexagrams from './data/hexagrams.json';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      line1: null,
      line2: null,
      line3: null,
      line4: null,
      line5: null,
      line6: null,
      reading: null
    }
    this.generateCasting();
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          iChingMe
        </header>
        <button className="cast-btn" onClick={() => this.generateCasting()}>Cast</button>
        <div className="Casting">
          <LineComponent className="Line-6" lineNumber="6" value={this.state.line6} />
          <LineComponent className="Line-5" lineNumber="5" value={this.state.line5} />
          <LineComponent className="Line-4" lineNumber="4" value={this.state.line4} />
          <LineComponent className="Line-3" lineNumber="3" value={this.state.line3} />
          <LineComponent className="Line-2" lineNumber="2" value={this.state.line2} />
          <LineComponent className="Line-1" lineNumber="1" value={this.state.line1} />
        </div>
        <hr />
        <CastingComponent className="Casting" reading={this.state.reading} />
        <hr />
        <div>
          {JSON.stringify(this.state.reading)}
        </div>
      </div>
    );
  }

  /*
6, 8 are broken
7, 9 solid
  */
  generateCasting() {
    console.log('heres the casting');
    const line1 = this.castLine();
    const line2 = this.castLine();
    const line3 = this.castLine();
    const line4 = this.castLine();
    const line5 = this.castLine();
    const line6 = this.castLine();
    // this.setState({
    //   line1,
    //   line2,
    //   line3,
    //   line4,
    //   line5,
    //   line6
    // });
    this.state.line1 = line1;
    this.state.line2 = line2;
    this.state.line3 = line3;
    this.state.line4 = line4;
    this.state.line5 = line5;
    this.state.line6 = line6;

    /**
     * 6 === changing 8 "- -" broken line
     * 9 === changing 7 "---" solid line
     * 6 - changing broken line, old yin
     * 8 - unchanging broken line, young yin
     * 7 - unchanging solid line, young yang
     * 9 - changing solid line, old yang
     */

    const lines = [line1, line2, line3, line4, line5, line6];
    const changedLines = lines.map(line => {
      if (line === 7) {
        return 9;
      } else if (line === 8) {
        return 6;
      }
      return line;
    }).join('');

    this.state.reading = hexagrams.find(h => h.changingLines === changedLines);
  }

  castLine() {
    return (this.throwCoin() + this.throwCoin() + this.throwCoin());
  }

  throwCoin() {
    return Math.floor(Math.random() * (4 - 2)) + 2;
  }
}

export default App;



